import styled from 'styled-components'
import {NavLink} from 'react-router-dom'
import navBack from '../../../images/navigation.svg'


export let Container = styled.div`
    position: fixed;
    bottom: 39px;
    display: flex;
    width: 100%;
    justify-content: center;
`
export let NavContainer = styled.div`
    width: 319px;
    height: 100px;
    background-image:  url(${navBack});
    background-repeat: no-repeat;
    filter: drop-shadow(0 0 20px rgba(0,0,0,0.25));
    display: flex;
    justify-content: space-evenly;
    align-items: center;
`

export let NavIcon = styled(NavLink)`
div {
width: 33px;
margin-top: 21px;
}
`

export let AddAdIcon = styled(NavLink)`

`